.card__wrapper{
    max-width: 1100px;
    margin: auto;
    display: flex;
    padding: 1rem;
    flex-wrap: wrap;
    gap: 1rem;
    background-color: #014E56;
}
.card__wrapper .card{
    margin: auto;
    width: 23%;
    font-family: Livvic;
    text-align: center;
    padding: 1rem;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    justify-content: space-between;
    color: #79C8C7;
    background-color:  #012F34;
}
@media (max-width: 800px){
    .card__wrapper .card{
      width: 48%;
    }
}
.card__wrapper .card h1{
    top: -50px;
    color: orange;
    border-radius: 20px;
}
