header{
    padding: 1rem;
}
header nav{
    max-width: 1110px;
    margin: auto;
    display: flex;
    font-family: Livvic;
    justify-content: space-between;
    background-color: #014E56;
    gap: 2rem;
   
}
header nav ul{
    display: flex;
    align-items: center;
    gap: 1rem;
}

header nav ul h2{
    color: white;
}
header nav ul li {
    display: flex;
}
header nav ul li a{
    color: white;
    list-style: none;
    text-decoration: none;   
}
ul button{
    width: 130px;
    height: 40px;
    font-family: Livvic;
    border: 2px solid white;
    background-color: #014E56;
    color: white;
    font-size: 14px;
    cursor: pointer;
    border-radius: 20px;
    margin-left: 500px;
}