section{
    width: 100%;
    padding: 1rem;
    background-color: #014E56;
    max-width: 1100px;
    margin: auto;
    position: relative;
}
section .text{
    margin: 0 30px;
    font-family: Livvic;
    font-weight: 700;
    color: white;
    font-size: 45px;
    font-weight: 700;
    line-height: 100px;
}
section .p{
    width: 50px;
    height: 4px;
    background-color:#F67E7E;
}
section .text__p{
    width: 30%;
    font-family: Livvic;
    font-weight: 600;
    color: white;
    margin-left: 500px;
    margin-top: -100px;
}
section img{
    position: absolute;
    top: 130px;
    left: 1030px;
}